				uni.setStorageSync('token', res.data);
				this.gToastSuccess(res.msg);
				getApp().globalData.gUserInfo.signedNotice = true;
				this.$emit('readed');
				this.gLoading(this, false);
			})
			.catch(err => {
				this.gLoading(this, false);
			});
		}
	}
};
</script>

